import React from 'react';
import {compose} from "@web3game/common/utils/compose";
import {hot} from "react-hot-loader";
import {getLang} from "@web3game/lang";
import {Button} from "react-onsenui";

export type SubBlockTitleProps = {
    children?: React.ReactElement | React.ReactElement[] | string | number | any;
    className?: string;
    onButtonClick?: () => void;
    buttonText?: string;
    disabled? : boolean;
};

export const SubBlockTitlePure = (
    {
        children,
        className = '',
        buttonText,
        onButtonClick,
        disabled,
    }: SubBlockTitleProps
) => {
    return (
        <div className={`subBlockTitle ${className}`}>
            <span className="withBullet" />
            {children}
            {
                buttonText && (
                    <Button className="pull-right" onClick={onButtonClick} disabled={disabled}>
                        {buttonText}
                    </Button>
                )
            }
        </div>
    );
};

export const SubBlockTitle = compose<SubBlockTitleProps>(
    hot(module),
    React.memo,
)(SubBlockTitlePure);
