import React, {CSSProperties, useState} from 'react';
import {List, ListItem, Page} from "react-onsenui";
import {compose} from "@web3game/common/utils/compose";
import classNames from 'classnames';
import {hot} from "react-hot-loader";
import {getLang} from "@web3game/lang";
import {withAPI} from "@web3game/common/components/withAPI";
import {watchAccountChange} from "@web3game/common/utils/watchAccountChange";
import {PageProps} from "@web3game/typings/common";
import {getHomeAPI} from "@web3game/apis/home/getHomeAPI";
import {RankInfo, RankResponse} from "@web3game/typings/home";
import {PagePadder} from "@web3game/common/components/PagePadder";
import RankHeaderImageUrl from '../../styles/app/rank/headerBack@3x.png';
import {Flex} from "../common/Flex";

import {Block} from "../common/Block";

import RankImage1 from '../../styles/app/rank/1@3x.png';
import RankImage2 from '../../styles/app/rank/2@3x.png';
import RankImage3 from '../../styles/app/rank/3@3x.png';
import { getCommonAPI } from '@web3game/apis/common/getCommonAPI';

export type GameTabPageProps = PageProps & {
    account?: string;
    gamePlayRankList?: RankResponse;
    referRankList?: RankResponse;
};

export function getTop3RankImage(index: number) {
    switch (index) {
        case 0:
            return RankImage1;

        case 1:
            return RankImage2;

        case 2:
            return RankImage3;
    }

    return '';
}

enum RankPageTabName {
    referer = 'referer',
    gamePlay = 'gamePlay',
}

const styles = {
    rankIndex: { width: 30 },
    rankName: ({ width: '100%' } as CSSProperties),
    rankAmount: { width: 160 },
};

export function GameTabPagePure(
    {
        account,
        gamePlayRankList,
        referRankList,
    }: GameTabPageProps,
) {

    const [activeTab, setActiveTab] = useState(RankPageTabName.referer);

    const {
        myRank = { amount: 0 },
        rankList = [],
    } = (
        activeTab === RankPageTabName.referer
            ? referRankList
            : gamePlayRankList
    ) || {};
    
    const renderRow = (rank: RankInfo, index: number) => {
        const rankImage = getTop3RankImage(index);
        const currentAccount = 
            account 
            && account !== '0x0000000000000000000000000000000000000000' 
            && account.slice(0, 8).concat('...').concat(account.slice(34)) === rank.account;
        return (
            <ListItem key={index} style={{ height: 70, fontSize: 12, backgroundColor: currentAccount ? "rgb(211, 255, 180)" : ""}}>
                <Flex>
                    <div style={styles.rankIndex} className="flexCenter">
                        {rankImage ? (
                            <img
                                src={rankImage}
                                alt={`${rank.amount}`}
                                style={{ width: '100%' }}
                            />
                        ) : index + 1}
                    </div>
                    <div style={styles.rankName} className="flexCenter">
                        {rank.account}
                    </div>
                    <div style={styles.rankAmount} className="flexCenter">
                        {rank.amount}
                    </div>
                </Flex>
            </ListItem>
        );
    };

    return (
        <Page>
            <PagePadder />
            <Block noOffset transparent>
                <img src={RankHeaderImageUrl} alt="Header Background" style={{ width: '100%' }} />
            </Block>
            <Block
                noOffset
                doubleHorizontalMargin
                style={{
                    marginTop: -10,
                    borderRadius: 10,
                }}
            >
                <Flex className="rankTabWrapper" fullHeight>
                    <Block
                        noMargin
                        transparent
                        doubleVerticalPadding
                        oneBottomPadding
                        className={classNames(
                            'flexCenter',
                            'rankTabItem',
                            { isActive: activeTab === RankPageTabName.referer }
                        )}
                    >
                        <a href="javascript:" onClick={() => setActiveTab(RankPageTabName.referer)}>
                            {getLang().HOME_PAGE.STRAIT_REFERER_RANK}
                        </a>
                    </Block>

                    <Block
                        noMargin
                        transparent
                        doubleVerticalPadding
                        oneBottomPadding
                        className={classNames(
                            'flexCenter',
                            'rankTabItem',
                            { isActive: activeTab === RankPageTabName.gamePlay }
                        )}
                    >
                        <a
                            href="javascript:"
                            onClick={() => setActiveTab(RankPageTabName.gamePlay)}
                        >
                            {getLang().HOME_PAGE.GAME_PLAY_RANK}
                        </a>
                    </Block>
                </Flex>
            </Block>
            <Block noOffset transparent oneVerticalPadding textCenter>
                {activeTab === RankPageTabName.gamePlay 
                    ? getLang().HOME_PAGE.MY_BET 
                    : getLang().HOME_PAGE.MY_PERFORMANCE
                } {myRank.amount} TTi
            </Block>
            <Block noMargin doubleHorizontalMargin doubleVerticalPadding style={{fontSize: 14}}>
                <Flex>
                    <div style={styles.rankIndex} className="flexCenter">#</div>
                    <div style={styles.rankName} className="flexCenter">
                        {getLang().HOME_PAGE.RANK_ACCOUNT}
                    </div>
                    <div style={styles.rankAmount} className="flexCenter">
                        {getLang().HOME_PAGE.RANK_AMOUNT} (TTi)
                    </div>
                </Flex>
            </Block>
            <div style={{ borderBottom: '1px solid #efeff4' }} />
            <Block noPadding doubleHorizontalMargin>
                <List
                    dataSource={rankList}
                    renderRow={renderRow}
                />
            </Block>
        </Page>
    );
}

export const RankTabPage = compose<GameTabPageProps>(
    hot(module),
    watchAccountChange,
    withAPI(async () => {
        const commonAPI = await getCommonAPI();
        const [account] = await commonAPI.getAccount();
        const homeAPI = await getHomeAPI();
        const referRankList = await homeAPI.getRefererRankList();
        const gamePlayRankList = await homeAPI.getGamePlayRankList();

        return {
            account,
            referRankList,
            gamePlayRankList,
        };
    }),
)(GameTabPagePure);

export const renderRankTabPage = ({ navigator }: GameTabPageProps) => (
    <RankTabPage
        key="rankTabPage"
        navigator={navigator}
    />
);

