import React, {useCallback, useState} from 'react';
import _ from 'lodash';
import {Button, Icon, Modal, Toast} from "react-onsenui";
import {getLang} from "@web3game/lang";
import {compose} from "@web3game/common/utils/compose";
import {hot} from "react-hot-loader";
import {withAPI} from "@web3game/common/components/withAPI";
import {getCommonAPI} from "@web3game/apis/common/getCommonAPI";
import {watchAccountChange} from "@web3game/common/utils/watchAccountChange";
import {pushPage} from "@web3game/common/utils/navigatorHelper";
import {PageProps} from "@web3game/typings/common";
import Clipboard from 'react-clipboard.js';
import qrcode from 'qrcode-generator';


import {Block} from "../../common/Block";
import {BlockTitle} from "../../common/BlockTitle";
import {Flex} from "../../common/Flex";
import {Splitter} from "../../common/Splitter";
import {getRoutes} from "../../../routes/getRoutes";
import DonationImageUrl from '../../../styles/app/home/donation@3x.png';

type MyAssetCardProps = {
    account?: string;
    tokenBalance?: any;
    ttBalance?: any;
    hasDonation?: boolean;
} & PageProps;

const getAccountQrCodeUrl = (account: string) => {
    if (!account) {
        return null;
    }

    const qr = qrcode(8, 'L');
    qr.addData(`${account}`);
    qr.make();

    return qr.createDataURL(4);
};

export function MyAssetCardPure(
    {
        account = '',
        tokenBalance = 0,
        ttBalance = 0,
        hasDonation = true,
        navigator,
    }: MyAssetCardProps
) {
    const showTransferPage = useCallback(() => pushPage(navigator, getRoutes().Transfer), []);
    const [toastText, setToastText] = useState('');
    const showToast = (text: string) => {
        setToastText(text);
        setTimeout(() => setToastText(''), 1000);
    };
    const qrCodeURL = getAccountQrCodeUrl(account);
    const [isQrCodeVisible, setIsQrCodeVisible] = useState(false);
    const showQrCode = useCallback(() => setIsQrCodeVisible(true), []);
    const hideQrCode = useCallback(() => setIsQrCodeVisible(false), []);

    return (
        <Block className="myAssertCard">
            {
                qrCodeURL && (
                    <Modal
                        isOpen={isQrCodeVisible}
                        onDeviceBackButton={hideQrCode}
                    >
                        <div
                            onClick={hideQrCode}
                            style={{ position: 'absolute', top: 30, right: 30 }}
                        >
                            <Icon icon="fa-close" style={{ fontSize: 30 }} />
                        </div>
                        <img
                            src={qrCodeURL}
                            alt="qrCode"
                        />
                    </Modal>
                )
            }
            <BlockTitle>
                {getLang().HOME_PAGE.MY_ASSET}


                <Clipboard
                    component="a"
                    data-clipboard-text={account}
                >
                    <span className="accountText" onClick={() => showToast(`${getLang().COPIED} ${account}`)}>
                        {account}
                        {"  "}
                        <Icon
                            icon="md-copy"
                        />
                    </span>
                </Clipboard>

                <Toast isOpen={!!toastText}>
                    <span>{toastText}</span>
                </Toast>
            </BlockTitle>
            <Block withBoxShadow noPadding noMargin oneBottomPadding>

                <Block noPadding noMargin oneMarginBottom>
                    <Flex>
                        <Block textCenter noPadding noMargin>
                            <Block noPadding noMargin oneMarginBottom className="myAssetBlockLabel" style={{fontSize: "12px"}}>
                                {getLang().TT_BALANCE}
                            </Block>
                            <Block noPadding noMargin>
                                <span className="textMainColor myAssetBlockNumber">
                                    {_.ceil(ttBalance, 3)}
                                </span>
                            </Block>
                        </Block>
                        <Block textCenter noPadding noMargin>
                            <Block noPadding noMargin oneMarginBottom className="myAssetBlockLabel" style={{fontSize: "12px"}}>
                                {getLang().TOKEN_BALANCE}
                            </Block>
                            <Block noPadding noMargin>
                                <span className="textMainColor myAssetBlockNumber">
                                    {_.ceil(tokenBalance, 3)}
                                </span>
                            </Block>
                        </Block>
                    </Flex>
                </Block>
                <Flex>
                    <div className="buttonGroup">
                        <Button onClick={showQrCode}>
                            {getLang().HOME_PAGE.RECEIVE}
                        </Button>
                        <Splitter splitterWrapper />
                        <Button onClick={showTransferPage}>
                            {getLang().HOME_PAGE.TRANSFER}
                        </Button>
                    </div>
                </Flex>
            </Block>

            {
                hasDonation && (
                    <Block noPadding noMargin doubleMarginTop>
                        <img alt="donation" src={DonationImageUrl} style={{ width: '100%' }} />
                    </Block>
                )
            }

        </Block>
    );
}

export const MyAssetCard = compose<MyAssetCardProps>(
    hot(module),
    React.memo,
    watchAccountChange,
    withAPI(
        async () => {
            const commonAPI = await getCommonAPI();
            const tokenBalance = await commonAPI.getTokenBalance();
            const ttBalance = await commonAPI.getTTBalance();
            const account = await commonAPI.getAccount();

            return {
                tokenBalance,
                ttBalance,
                account,
            };
        },
    )
)(MyAssetCardPure);
