import React from 'react';
import {compose} from "@web3game/common/utils/compose";
import {hot} from "react-hot-loader";

export type BlockTitleProps = {
    children?: React.ReactElement | any[] | string | number;
    className?: string;
    onShowDetail?: () => void;
    icon?: React.ReactElement | string;
};

export const BlockTitlePure = (
    {
        children,
        className = '',

    }: BlockTitleProps
) => {
    return (
        <h2 className={`blockTitle ${className}`}>
            {children}
        </h2>
    );
};

export const BlockTitle = compose<BlockTitleProps>(
    hot(module),
    React.memo,
)(BlockTitlePure);
