import React from 'react';
import {Button} from "react-onsenui";
import {getLang} from "@web3game/lang";
import {compose} from "@web3game/common/utils/compose";
import {hot} from "react-hot-loader";
import {watchAccountChange} from "@web3game/common/utils/watchAccountChange";
import {withAPI} from "@web3game/common/components/withAPI";
import {MyTaskInfo, TaskInfo} from "@web3game/typings/home";
import {getHomeAPI} from "@web3game/apis/home/getHomeAPI";
import {Block} from "../../common/Block";
import {BlockTitle} from "../../common/BlockTitle";
import {SubBlockTitle} from "../../common/SubBlockTitle";
import {Splitter} from "../../common/Splitter";
import {Flex} from "../../common/Flex";
import { on } from 'cluster';

type MyTaskCardProps = {
    todayTotalIncome?: number;
    taskList?: MyTaskInfo[];
    latestGain?: string;
    onGainTaskEarning?: () => Promise<void>;
    onReleaseScroll?: (task: MyTaskInfo) => Promise<void>;
    onRenewScroll?: (task: MyTaskInfo) => Promise<void>;
};



export function MyTaskCardPure(
    {
        taskList = [],
        todayTotalIncome = 0,
        latestGain = '',
        onGainTaskEarning,
        onReleaseScroll,
        onRenewScroll,
    }: MyTaskCardProps
) {

    const renderTaskDetail = (task: MyTaskInfo, index: number) => {
        return (
            <Block key={index} withBoxShadow noPadding oneVerticalPadding>
                <Flex>
                    <div style={{width: 40, margin: "20px 0 0 0"}}>
                        <span className="textCenter" style={{fontSize: 12}}>
                            {task.name}
                        </span>
                    </div>
                    <Splitter />
                    <div style={{width: 76}}>
                        <div className="textMainColor">
                            <span style={{fontSize: 20}}>{task.rate}%</span>
                            <span style={{fontSize: 12, paddingLeft: 3}}>
                                {getLang().TASK.PROFIT}
                            </span>
                        </div>
                        <div className="textCenter">
                            {task.times} {getLang().HOME_PAGE.EARNING_TIMES}
                        </div>
                    </div>
                    <Splitter />
                    <div className="textCenter" style={{width: 130, fontSize: 12}}>
                        <div>
                            {task.remainTimes} {getLang().HOME_PAGE.AVAILABLE_TIMES}
                        </div>
                        <div>
                            {getLang().HOME_PAGE.STATIC_PROFIT_OF_ONE_TIME} 
                            {task.staticProfitOfOneTime} TTi
                        </div>
                        <div>
                            {getLang().HOME_PAGE.STATIC_PROFIT_TODAY} 
                            {task.staticProfit} TTi
                        </div>
                    </div>
                    <Splitter />
                    <div>
                        <Button
                            className="small"
                            onClick={() => onReleaseScroll && onReleaseScroll(task)}
                        >
                            {getLang().HOME_PAGE.RELEASE_SCROLL}
                        </Button>
                        <br />
                        <br />
                        <Button
                            className="small"
                            onClick={() => onRenewScroll && onRenewScroll(task)} 
                            disabled={task.remainTimes !== 0}
                        >
                            {getLang().HOME_PAGE.RENEW_SCROLL}
                        </Button>
                    </div>
                </Flex>
            </Block>
        );
    };

    return (
        <Block className="myTaskCard" oneTopPadding>
            <BlockTitle>
                {getLang().HOME_PAGE.MY_TASK_TITLE}
            </BlockTitle>
            <SubBlockTitle 
                buttonText={getLang().GAIN} 
                onButtonClick={onGainTaskEarning} 
                disabled={todayTotalIncome === 0}
            >
                <span style={{fontSize: "9px"}}>{getLang().HOME_PAGE.TODAY_TOTAL_IN_COME}</span>
                {"  "}
                <span className="textMainColor">
                    {todayTotalIncome} TTi
                </span>
                {"  "}
                <span style={{fontSize: "9px"}}>
                    {getLang().HOME_PAGE.LATEST_GAIN} 
                </span>
                <span className="textMainColor">
                    {latestGain}
                </span>
            </SubBlockTitle>
            {taskList.map(renderTaskDetail)}
        </Block>
    );
}

export const MyTaskCard = compose<MyTaskCardProps>(
    hot(module),
    React.memo,
    watchAccountChange,
    withAPI(
        async ({updateDataVersion}) => {
            const homeAPI = await getHomeAPI();
            const { taskList, todayTotalIncome, latestGain } = await homeAPI.getMyTaskResponse();
            const onGainTaskEarning = async () => {
                try {
                    await homeAPI.gainTaskEarning();
                    updateDataVersion();
                } catch (e) {
                    alert(e.message);
                }
            };

            const onReleaseScroll = async (task: MyTaskInfo) => {
                try {
                    if (task.remainTimes > 0) {
                        if (!confirm(getLang().TAKEOFF_TIP)) {
                            return;
                        }
                    }
                    await homeAPI.releaseScroll(task);
                    updateDataVersion();
                } catch (e) {
                    alert(e.message);
                }
            };

            const onRenewScroll = async (task: MyTaskInfo) => {
                try {
                    await homeAPI.renewScroll(task);
                    updateDataVersion();
                } catch (e) {
                    alert(e.message);
                }
            };


            return { 
                taskList, 
                todayTotalIncome, 
                latestGain, 
                onGainTaskEarning, 
                onReleaseScroll, 
                onRenewScroll, 
            };
        },
    ),
)(MyTaskCardPure);
