import React, {useCallback} from 'react';
import {pushPage} from "@web3game/common/utils/navigatorHelper";
import {Navigator} from "react-onsenui";

import {Block} from "../../common/Block";
import DonationImageUrl from '../../../styles/app/home/donation@3x.png';
import {getRoutes} from "../../../routes/getRoutes";

export type DonateCardProps = {
    navigator: Navigator;
};


export function DonateCard(
    {
        navigator,
    }: DonateCardProps
) {
    const showDonatePage = useCallback(() => pushPage(navigator, getRoutes().Donate), []);
    return (
        <Block noPadding noMargin doubleMarginTop>
            <div
                onClick={showDonatePage}
            >
                <img alt="donation" src={DonationImageUrl} style={{ width: '100%' }} />
            </div>
        </Block>
    );
}

