import React, {useCallback, useState} from 'react';
import {hot} from 'react-hot-loader';
import {getLang} from "@web3game/lang";
import {Navigator, Page, Tab, Tabbar} from 'react-onsenui'; // Only import the necessary components
import {observer} from 'mobx-react';
import {compose} from "@web3game/common/utils/compose";
import {pushPage} from "@web3game/common/utils/navigatorHelper";
import {watchAccountChange} from "../common/utils/watchAccountChange";
import {renderMyAccountTabPage} from "./components/accountTab/renderMyAccountTabPage";
import {renderHomeTabPage} from "./components/homeTab/renderHomeTabPage";
import {renderRankTabPage} from "./components/rankTab/renderRankTabPage";
import {renderTaskTabPage} from "./components/taskTab/renderTaskTabPage";

// @ts-ignore
import('./styles/themify-icons/themify-icons.css');

// node_modules/onsenui/css/ionicons/css/ionicons.min.css


export type HomePageProps = {
    navigator: Navigator;
    locale?: string;
};

export function HomePage({ navigator, locale }: HomePageProps) {
    const [tabIndex, setTabIndex] = useState(0);
    const handlePushPage = useCallback((router) => {
        pushPage(navigator, router);
    }, []);

    const renderTabs = (activeIndex: number, tabbar: any) => [
        {
            content: renderHomeTabPage({navigator}),
            tab: <Tab key="home" label={getLang().HOME} icon="md-home" />,
        },
        {
            content: renderTaskTabPage({ navigator }),
            tab: <Tab key="tasks" label={getLang().TASK.TASK} icon="ion-chatbox-working" />,
        },
        {
            content: renderRankTabPage({ navigator }),
            tab: <Tab key="settings" label={getLang().RANK} icon="ion-stats-bars" />,
        },
        {
            content: renderMyAccountTabPage({ pushPage: handlePushPage, navigator }),
            tab: <Tab key="myAccount" label={getLang().MY} icon="md-account" />,
        },
    ];

    const renderFixed = () => (
        <Tabbar
            // @ts-ignore
            onPreChange={({ activeIndex }) => setTabIndex(activeIndex)}
            position="top"
            index={tabIndex}

            // @ts-ignore
            renderTabs={renderTabs}
        />
    );

    return (
        <Page renderFixed={renderFixed} />
    );
}

export default compose<HomePageProps>(
    hot(module),
    observer,
    watchAccountChange,
)(HomePage);
