import React from 'react';
import {hot} from "react-hot-loader";
import {Navigator, Page} from "react-onsenui";
import {PagePadder} from "@web3game/common/components/PagePadder";
import {compose} from "@web3game/common/utils/compose";
import {withAPI} from "@web3game/common/components/withAPI";
import {watchAccountChange} from "../../../common/utils/watchAccountChange";
import {MyTaskCard} from "./components/MyTaskCard";
import {MyQuoterBonusCard} from "./components/MyQuoterBonusCard";
import {GameCard} from "./components/GameCard";
import {Slides} from "./components/Slides";
import { DonateCard } from './components/DonateCard';

export type HomeTabPageProps = {
    navigator: Navigator;
};


function HomeTabPagePure(
    {
        navigator,
    }: HomeTabPageProps,
) {
    return (
        <Page key="homeTabPage">
            <PagePadder />
            <Slides />
            <GameCard navigator={navigator} />
            {/* <MyAssetCard navigator={navigator} /> */}
            <DonateCard navigator={navigator} />
            <MyTaskCard />
            <MyQuoterBonusCard />
            <div className="bottomLine" />
        </Page>
    );
}

const HomeTabPage = compose<HomeTabPageProps>(
    hot(module),
    watchAccountChange,
    withAPI(async () => {
        return {};
    }),
)(HomeTabPagePure);

export const renderHomeTabPage = ({ navigator }: HomeTabPageProps) => (
    <HomeTabPage
        key="home"
        navigator={navigator}
    />
);

