import React, {useCallback} from 'react';
import {pushPage} from "@web3game/common/utils/navigatorHelper";
import {Navigator} from "react-onsenui";

import {Block} from "../../common/Block";
import {Flex} from "../../common/Flex";
import OpenBoxImageUrl from '../../../styles/app/home/game/openBox@3x.png';
import NiuniuImageUrl from '../../../styles/app/home/game/niuniu@3x.png';
import CaiCaiImageUrl from '../../../styles/app/home/game/caicaiheibai@3x.png';
import MeishizuqiuImageUrl from '../../../styles/app/home/game/meishizuqiuheibai@3x.png';
import {getRoutes} from "../../../routes/getRoutes";

export type GameCardProps = {
    navigator: Navigator;
};


export function GameCard(
    {
        navigator,
    }: GameCardProps
) {
    const showOpenBoxPage = useCallback(() => pushPage(navigator, getRoutes().OpenBox), []);

    return (
        <Block className="gameCard" noMargin oneBottomPadding>
            <Flex>
                <div
                    onClick={showOpenBoxPage}
                >
                    <img alt="OpenBox" src={OpenBoxImageUrl} />
                </div>
                <div onClick={() => alert('正在紧张开发中...')}>
                    <img alt="NiuNiu" src={NiuniuImageUrl} />
                </div>
            </Flex>
            <Flex>
                <div>
                    <img alt="CaiCai" src={CaiCaiImageUrl} />
                </div>
                <div>
                    <img alt="Meishizuqiu" src={MeishizuqiuImageUrl} />
                </div>
            </Flex>
        </Block>
    );
}

