import React from 'react';
import {createAsyncPage} from "@web3game/common/utils/createAsyncPage";
import {getLang} from "@web3game/lang";
import {LoaderType} from "@web3game/common";
import {
    loadAboutUSPage, loadAnnouncementPage,
    loadHomePage,
    loadInvitationPage,
    loadLanguageSelectorPage, loadMyEarningPage,
    loadOpenBoxGame,
    loadTransferPage,
    loadTradingLogPage,
    loadDonatePage,
} from "./asyncComponents";

export const routeNames = {
    Home: 'Home',
    LanguageSelector: 'LanguageSelector',
    Transfer: 'Transfer',
    Invitation: 'Invitation',
    AboutUS: 'AboutUS',
    TradingLog: 'TradingLog',
    MyEarning: 'MyEarning',
    SystemAnnouncement: 'SystemAnnouncement',
    OpenBox: 'OpenBox',
    Donate: 'Donate',
};

type RouteItemOption = {
    key: string;
    title: string;
    loader: LoaderType;
    asyncComponent?: React.ComponentType;
}

type RouteItem = RouteItemOption & {
    component: any;
    preLoad: () => Promise<any>;
};

function createRouteItem({key, title, loader}: RouteItemOption): RouteItem {
    return {
        key,
        component: createAsyncPage(loader),
        title,
        loader,
        preLoad: loader,
    };
}

type RouteNameKeyType = keyof (typeof routeNames);

type RoutesMap = {
    [key in RouteNameKeyType]: RouteItem;
};

export const getRoutes = (): RoutesMap => {
    const routesMap = {

        [routeNames.Home]: createRouteItem({
            key: routeNames.Home,
            loader: loadHomePage,
            title: getLang().HOME,
        }),

        [routeNames.LanguageSelector]: createRouteItem({
            key: routeNames.LanguageSelector,
            loader: loadLanguageSelectorPage,
            title: getLang().LANGUAGE_SETTING,
        }),

        [routeNames.Transfer]: createRouteItem({
            key: routeNames.Transfer,
            loader: loadTransferPage,
            title: getLang().HOME_PAGE.TRANSFER,
        }),

        [routeNames.Invitation]: createRouteItem({
            key: routeNames.Invitation,
            loader: loadInvitationPage,
            title: getLang().HOME_PAGE.INVITATION,
        }),

        [routeNames.AboutUS]: createRouteItem({
            key: routeNames.AboutUS,
            loader: loadAboutUSPage,
            title: getLang().HOME_PAGE.ABOUT_US,
        }),

        [routeNames.TradingLog]: createRouteItem({
            key: routeNames.TradingLog,
            loader: loadTradingLogPage,
            title: getLang().HOME_PAGE.TRADING_LOG,
        }),

        [routeNames.MyEarning]: createRouteItem({
            key: routeNames.MyEarning,
            loader: loadMyEarningPage,
            title: getLang().HOME_PAGE.MY_EARNING,
        }),

        [routeNames.SystemAnnouncement]: createRouteItem({
            key: routeNames.SystemAnnouncement,
            loader: loadAnnouncementPage,
            title: getLang().HOME_PAGE.SYSTEM_ANNOUNCEMENT,
        }),

        [routeNames.OpenBox]: createRouteItem({
            key: routeNames.OpenBox,
            loader: loadOpenBoxGame,
            title: getLang().GAME,
        }),

        [routeNames.Donate]: createRouteItem({
            key: routeNames.Donate,
            loader: loadDonatePage,
            title: getLang().DONATE.DONATE,
        }),

    };

    return routesMap as RoutesMap;
};
