import React from 'react';
import {hot} from "react-hot-loader";
import {List, ListItem, Page} from "react-onsenui";
import {getLang} from "@web3game/lang";
import {PagePadder} from "@web3game/common/components/PagePadder";
import {compose} from "@web3game/common/utils/compose";
import {watchAccountChange} from "../../../common/utils/watchAccountChange";
import {createNoBackHeaderRender} from "../Header";
import {getRoutes} from "../../routes/getRoutes";
import { PageProps } from '@web3game/typings/common';
import { MyAssetCard } from '../homeTab/components/MyAssetCard';
import { Block } from '../common/Block';

const getAccountPageList = () => [
    getRoutes().LanguageSelector,
    // getRoutes().MyEarning,
    getRoutes().TradingLog,
    getRoutes().Invitation,
    getRoutes().SystemAnnouncement,
    getRoutes().AboutUS,
];

export type MyAccountPageProps = PageProps & {
    pushPage: (router: any) => any;
};

function MyAccountPagePure(
{ 
    pushPage,
    navigator,
 }: MyAccountPageProps
 ) {

    return (
        <Page>
            <PagePadder />
            <Block transparent noMargin noPadding oneHorizontalMargin>
                <MyAssetCard navigator={navigator} hasDonation={false} />
            </Block>
            
            <List
                modifier="inset"
                dataSource={getAccountPageList()}
                renderRow={({ title, key, component }, idx) => (
                    <ListItem
                        key={idx}
                        modifier="chevron"
                        onClick={() => pushPage(
                            {
                                key,
                                component,
                            },
                        )}
                    >
                        <div className="center">{title}</div>
                    </ListItem>
                )}
            />
        </Page>
    );
}

export const MyAccountPage = compose<MyAccountPageProps>(
    hot(module),
    React.memo,
    watchAccountChange,
)(MyAccountPagePure);

export const renderMyAccountTabPage = ({ pushPage, navigator }: MyAccountPageProps) => (
    <MyAccountPage key="myAccount" pushPage={pushPage} navigator={navigator} />
);
