import React from 'react';
import {hot} from "react-hot-loader";
import {compose} from "@web3game/common/utils/compose";
import {Carousel} from "react-responsive-carousel";

import "react-responsive-carousel/lib/styles/carousel.min.css";

import Banner1Url from '../../../styles/app/home/slides/banne6@3x.png';
import Banner2Url from '../../../styles/app/home/slides/banne7@3x.png';
import Banner3Url from '../../../styles/app/home/slides/banne3@3x.png';
import Banner4Url from '../../../styles/app/home/slides/banne4@3x.png';
import Banner5Url from '../../../styles/app/home/slides/banne5@3x.png';

type SlideItemProps = { children: React.ReactElement | string };


export const SlideItem = (
    {
        children,
    }: SlideItemProps,
) => {
    return (
        <div style={{ height: "100%" }}>
            {children}
        </div>
    );
};

export function SlidesPure() {
    return (
        <Carousel
            showThumbs={false}
            showArrows={false}
            showStatus={false}
            autoPlay
            infiniteLoop
        >

            <SlideItem>
                <img alt="slide2" src={Banner2Url} />
            </SlideItem>

            <SlideItem>
                <img alt="slide3" src={Banner3Url} />
            </SlideItem>

            <SlideItem>
                <img alt="slide4" src={Banner4Url} />
            </SlideItem>

            <SlideItem>
                <img alt="slide4" src={Banner5Url} />
            </SlideItem>

            <SlideItem>
                <img alt="slide1" src={Banner1Url} />
            </SlideItem>
        </Carousel>
    );
}

export const Slides = compose(
    hot(module),
    React.memo,
)(SlidesPure);
