import React from 'react';
import {Button, List, ListItem, Navigator, Page} from "react-onsenui";
import {TaskInfo} from "@web3game/typings/home";
import {withAPI} from "@web3game/common/components/withAPI";
import {getHomeAPI} from "@web3game/apis/home/getHomeAPI";
import {compose} from "@web3game/common/utils/compose";
import {hot} from "react-hot-loader";
import {getLang} from "@web3game/lang";
import {Block} from "../common/Block";
import {Flex} from "../common/Flex";
import {watchAccountChange} from "../../../common/utils/watchAccountChange";
import { myIntroducer } from '@web3game/apis/contractHelps/introduceContract';

type ShopsTabPageProps = {
    navigator: Navigator;
    productList?: TaskInfo[];
    onAddTask?: (taskInfo: TaskInfo) => Promise<void>;
};


function ShopsTabPagePure(
    {
        productList = [],
        onAddTask,
    }: ShopsTabPageProps,
) {
    const addTask = (taskInfo: TaskInfo) => {
        if (onAddTask) {
            onAddTask(taskInfo);
        }
    };

    const renderRow = (task: TaskInfo, index: number) => {
        return (
            <ListItem key={index}>
                <Flex fullWidth>
                    <div className="left flexCenter" style={{ width: 90 }}>
                        <img src={task.imageUrl} style={{ width: '100%' }} alt={task.name} />
                    </div>
                    <Block noOffset doubleHorizontalPadding>
                        <div>{task.name}</div>
                        <div style={{ fontSize: 12 }}>
                            {task.rate}% {getLang().TASK.PROFIT}
                        </div>
                        <div style={{ fontSize: 12 }}>
                            {getLang().TASK.CAN_BUY} {task.canBuy}
                        </div>
                        <div style={{ fontSize: 12 }}>
                            {getLang().TASK.CAN_USE} {task.times}{getLang().TASK.TIMES}
                        </div>
                        <Block noOffset>
                            <span style={{fontSize: 12}}>{getLang().NEED}</span>
                            <span style={{ fontSize: 13 }} className="textMainColor textBolder">
                                {task.tti}
                            </span>
                            <span style={{fontSize: 12}}> TTi</span>
                        </Block>
                    </Block>
                    <div style={{ width: 80 }} className="flexCenter">
                        <Button
                            onClick={() => addTask(task)}
                            disabled={task.canBuy === 0}
                        >
                            {getLang().GET}
                        </Button>
                    </div>
                </Flex>
            </ListItem>
        );
    };
    return (
        <Page>
            <Block noOffset oneMarginTop oneHorizontalMargin>
                <List
                    dataSource={productList}
                    renderRow={renderRow}
                />
            </Block>
        </Page>
    );
}

const HomeTabPage = compose<ShopsTabPageProps>(
    hot(module),
    watchAccountChange,
    withAPI(
        async ({updateDataVersion}) => {
            const { getTaskList, addTask } = await getHomeAPI();
            const invitor = await myIntroducer();

            const onAddTask = async (taskInfo: TaskInfo) => {
                // 当前用户没有邀请人,无法领取任务
                if (invitor === '0x0000000000000000000000000000000000000000') {
                    alert(getLang().TASK.NO_INVITOR);
                    return;
                }
                try {
                    await addTask(taskInfo);
                    updateDataVersion();
                } catch (e) {
                    alert(e.message);
                }
            };

            return {
                productList: await getTaskList(),
                onAddTask,
            };
        },
    ),
)(ShopsTabPagePure);

export const renderTaskTabPage = ({ navigator }: ShopsTabPageProps) => (
    <HomeTabPage
        key="shops"
        navigator={navigator}
    />
);

